<template>
	<div class="loginBox">
		<div class="top">
			<div class="logo"></div>
			<div class="input">
				<label for="username">账号：</label>
				<input type="text" id="username" v-model="username">
			</div>
			<div class="input">
				<label for="password">密码：</label>
				<input type="text" id="password" v-model="password">
			</div>
		</div>
		<div class="btn">
			<button @click="login">登录</button>
		</div>
	</div>
</template>
<script>
	export default {
		data(){
			return {
				username:'',
				password:''
			}
		},
		//组件内的守卫，在进入这个路由前就会被触发（点击退出后会跳到这个页面，进来后就把用户的信息给删除了，就能实现退出的功能）
		beforeRouteEnter (to, from, next) {
			next(vm=>{
				//1、在本地存储里把存储的数据删除
				localStorage.removeItem('loginMessage');
				//2、更新stroe.js里的数据
				vm.$store.commit('storageUser',{userName:'',isLogin:false});
			});
		},
		methods:{
			login(){
				//alert(1);
				if(this.username=='' || this.password==''){
					alert('请输入用户名与密码');
					return;
				}

				this.$axios.get('https://wd0905222024oblsbc.wilddogio.com/users.json')
				.then(
					res=>{

						let logined=false;	//存储是否登录成功

						const keys=Object.keys(res.data);

						for(let v of keys){

							if(res.data[v].username==this.username && res.data[v].password==this.password){

								logined=true;

								//登录成功了，用户的信息要存储到localStorage里面
								const local={	//这里的格式一定要设置成JSON的格式，为了取的时候能转成真正的对象
									"userName":res.data[v].username,
									"isLogin":true,
								}
								localStorage.setItem('loginMessage',JSON.stringify(local));
								//同时，需要把数据发送到store.js当中
								this.$store.commit('storageUser',local)


								this.$router.push('/home');

								break;
								
							}else{
								logined=false;
							}
						}

						if(!logined){
							alert('用户名或者密码错误！');
						}
					}
				)
				.catch(
					err=>{
						console.log(err);
					}
				)
			}
		}
	}
</script>
